      census_data: {
        lazy: true,
        lazyLoad(node, resolve) {
          const { level } = node;

          if (level === 0) {
            // 
            getProvince().then((res) => {
              const nodes = res.data.map((item) => ({
                value: item.id,
                label: item.name,
                leaf: level >= 2,
              }));
              resolve(nodes);
            });
          }
          // 
          if (level === 1) {
            let limit = [34967];
            let limits = [34948, 34968];
            if (limit.includes(node.value)) {
              resolve([{ value: 34967, label: "", leaf: level >= 1 }]);
            }
            if (limits.includes(node.value)) {
              getCity(node.value).then((res) => {
                const nodes = res.data.map((item) => ({
                  value: item.id,
                  label: item.name,
                  leaf: level >= 1,
                }));
                resolve(nodes);
              });
            }
            getCity(node.value).then((res) => {
              const nodes = res.data.map((item) => ({
                value: item.id,
                label: item.name,
                leaf: level >= 2,
              }));
              resolve(nodes);
            });
          }
          // 
          if (level === 2) {
            getDistrict(node.value).then((res) => {
              const nodes = res.data.map((item) => ({
                value: item.id,
                label: item.name,
                leaf: level >= 2,
              }));
              resolve(nodes);
            });
          }
        },
